<template>
  <div id="avatars-component" :style="{ width: `${list.length * 20 + 10}px` }">
    <div class="avatars-wrapper">
      <div
        v-for="(item, index) in list"
        :key="item.id"
        class="avatar"
        :style="getStyles(item.profile_photo, index)"
      ></div>
    </div>
    <p class="dots" v-if="data.length > max" :style="{ left: `${max  * 20 + 10}px` }">...</p>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, computed } from "vue";
import * as _ from "ramda";
import { formatImageUrl } from "@/utils";
import defaultAvatarImage from "@/assets/images/icons/png/default-avatar.png";

const getStyles = (item: string, index: number) => {
  const styles: {
    left: string;
    backgroundImage?: string;
    backgroundColor?: string;
  } = {
    left: `${index * 20}px`
  };
  if (item) {
    styles.backgroundImage = `url("${formatImageUrl(item)}")`;
  } else {
    styles.backgroundImage = `url("${defaultAvatarImage}")`;
  }
  return styles;
};

const Component = defineComponent({
  name: "Avatars",
  props: {
    data: {
      type: Array,
      default: () => []
    },
    max: {
      type: Number,
      default: 10
    }
  },
  setup(props) {
    const { data, max } = toRefs(props);
    const list = computed(() => _.splitAt(max.value, data.value)[0]);
    return {
      list,

      getStyles
    };
  }
});
export default Component;
</script>

<style lang="stylus" scoped>
#avatars-component
  position relative
  height 30px
  .avatar
    position absolute
    top 50%
    transform translateY(-50%)
    box-sizing border-box
    width 30px
    height 30px
    border-radius 50%
    border 1px solid white
    background-size cover
    background-repeat no-repeat
    background-position center
  .dots
    position absolute
    top 50%
    transform translateY(-30%)
</style>
