<template>
  <div id="review-table-component">
    <table cellspacing="0" cellpadding="0">
      <thead>
        <tr>
          <th>
            <p>Project</p>
          </th>
          <th>
            <p>Member</p>
          </th>
          <th>
            <p>Stage</p>
          </th>
          <th>
            <p>Brand</p>
          </th>
          <th>
            <p>Date</p>
          </th>
          <th>
            <p>Source</p>
          </th>
          <th>
            <p>Status</p>
          </th>
        </tr>
        <tr class="spacer"></tr>
      </thead>
      <tbody v-if="list">
        <template v-for="item in list" :key="item.id">
          <tr class="item" @click="onClick(item.id)">
            <td>
              <p>{{ item.name }}</p>
            </td>
            <td>
              <div class="center">
                <Avatars :data="item.joined_member"></Avatars>
              </div>
            </td>
            <td>
              <p>{{ item.newest_stage_name }}</p>
            </td>
            <td>
              <p>{{ item.company.buyer.name }}</p>
            </td>
            <td>
              <p>{{ dayjs(item.create_at).format("YYYY-MM-DD") }}</p>
            </td>
            <td>
              <p>{{ item.source }}</p>
            </td>
            <td>
              <div class="center">
                <el-tag effect="dark" :color="getTagColor(item.status)">{{
                  item.status
                }}</el-tag>
              </div>
            </td>
          </tr>
          <tr class="spacer"></tr>
        </template>
      </tbody>
    </table>

    <div v-if="!list" class="empty">
      <p>No Data</p>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType, computed } from "vue";
import dayjs from "dayjs";
import Avatars from "@/components/Avatars.vue";
import { useRouter } from "vue-router";
import { Projects } from "@/typings/project";

const colors = ["#5470c6", "#91cc76", "#f8c858", "#e66", "#409eff"];
const getTagColor = (tag: string): string => {
  const adapter: Adapter<string> = {
    "In Progress": colors[0],
    "Completed on time": colors[4],
    "Completed but delayed": colors[1],
    Dropped: colors[3],
    Delayed: colors[2],
    default: ""
  };
  return adapter[tag] || adapter.default;
};

const Component = defineComponent({
  name: "ReviewTable",
  components: {
    Avatars
  },
  props: {
    data: {
      type: Object as PropType<Projects> | null,
      default: null
    }
  },
  setup(props) {
    const router = useRouter();
    const { data } = toRefs(props);

    const list = computed(() =>
      data.value && data.value.results.length ? data.value.results : null
    );

    const onClick = async (id: number) => {
      await router.push({ name: "ReviewDetail", params: { id } });
    };

    return {
      list,

      onClick,

      getTagColor,
      dayjs
    };
  }
});

export default Component;
</script>

