
import { defineComponent, toRefs, PropType, computed } from "vue";
import dayjs from "dayjs";
import Avatars from "@/components/Avatars.vue";
import { useRouter } from "vue-router";
import { Projects } from "@/typings/project";

const colors = ["#5470c6", "#91cc76", "#f8c858", "#e66", "#409eff"];
const getTagColor = (tag: string): string => {
  const adapter: Adapter<string> = {
    "In Progress": colors[0],
    "Completed on time": colors[4],
    "Completed but delayed": colors[1],
    Dropped: colors[3],
    Delayed: colors[2],
    default: ""
  };
  return adapter[tag] || adapter.default;
};

const Component = defineComponent({
  name: "ReviewTable",
  components: {
    Avatars
  },
  props: {
    data: {
      type: Object as PropType<Projects> | null,
      default: null
    }
  },
  setup(props) {
    const router = useRouter();
    const { data } = toRefs(props);

    const list = computed(() =>
      data.value && data.value.results.length ? data.value.results : null
    );

    const onClick = async (id: number) => {
      await router.push({ name: "ReviewDetail", params: { id } });
    };

    return {
      list,

      onClick,

      getTagColor,
      dayjs
    };
  }
});

export default Component;
