
import { defineComponent, toRefs, watch, nextTick, PropType } from "vue";
import dayjs from "dayjs";
import * as echarts from "echarts/core";

import {
  BarChart,
  BarSeriesOption,
  LineChart,
  LineSeriesOption
} from "echarts/charts";
import {
  GridComponent,
  GridComponentOption,
  TooltipComponent,
  TooltipComponentOption,
  ToolboxComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";
import { SuccessRate } from "@/typings/review";

type ECOption = echarts.ComposeOption<
  | BarSeriesOption
  | LineSeriesOption
  | GridComponentOption
  | TooltipComponentOption
  | ToolboxComponentOption
>;

echarts.use([
  BarChart,
  LineChart,
  GridComponent,
  CanvasRenderer,
  TooltipComponent
]);

const colors = ["#5470c6", "#91cc76", "#f8c858", "#e66"];

const Component = defineComponent({
  name: "SuccessRateChart",
  props: {
    data: {
      type: Object as PropType<SuccessRate> | null,
      default: null
    }
  },
  setup(props) {
    const { data } = toRefs(props);

    watch<[SuccessRate]>([data], async ([item]) => {
      const option: ECOption = {
        grid: {
          left: 25,
          right: 40,
          bottom: 20,
          top: 40
        },
        tooltip: {
          trigger: "axis",
          formatter(params: any) {
            return `
              ${params[0].name} <br />
              ${params[0].marker} ${params[0].seriesName} ${params[0].value} <br />
              ${params[1].marker} ${params[1].seriesName} ${params[1].value} <br />
              ${params[2].marker} ${params[2].seriesName} ${params[2].value}% <br />
            `
          }
        },
        xAxis: {
          type: "category",
          data: item.name.map((name: string) =>
            dayjs(name).format("YYYY-MM-DD")
          )
        },
        yAxis: [
          {
            type: "value",
            name: "Count",
            splitLine: {
              show: false
            }
          },
          {
            type: "value",
            name: "Percentage",
            min: 0,
            max: 100,
            axisLabel: {
              formatter: "{value}%"
            }
          }
        ],
        series: [
          {
            name: "Fail",
            type: "bar",
            stack: "one",
            label: {
              show: true
            },
            itemStyle: {
              color: colors[3]
            },
            barMaxWidth: 40,
            data: item.fail
          },
          {
            name: "Success",
            type: "bar",
            stack: "one",
            label: {
              show: true
            },
            itemStyle: {
              color: colors[0]
            },
            barMaxWidth: 40,
            data: item.success
          },
          {
            name: "Success Rate",
            type: "line",
            data: item.success_rate.map((rate: number) => rate * 100),
            itemStyle: {
              color: colors[2]
            },
            yAxisIndex: 1
          }
        ]
      };

      await nextTick(() => {
        const chart = echarts.init(
          document.getElementById("success-rate-chart-main")!
        );
        chart.setOption(option);
      });
    });
  }
});

export default Component;
