import request from "@/apis/http";
import { ReviewsParams } from "@/typings/review";

export function getReviewStatusPie(params: ReviewsParams) {
  return request({
    url: `/projects_review/status_pie/`,
    method: "GET",
    params
  });
}

export function getReviewSourceRatio(params: ReviewsParams) {
  return request({
    url: `/projects_review/source_ratio/`,
    method: "GET",
    params
  });
}

export function getReviewSuccessRate(params: ReviewsParams) {
  return request({
    url: `/projects_review/success_rate/`,
    method: "GET",
    params
  });
}

export function getReviewTimeConsuming(id: number) {
  return request({
    url: `/projects_review/${id}/time_consuming/`,
    method: "GET"
  });
}

export function getReviewStagesRatio(id: number) {
  return request({
    url: `/projects_review/${id}/stages_ratio/`,
    method: "GET"
  });
}

export function getReviewCommentsRatio(id: number) {
  return request({
    url: `/projects_review/${id}/comments_ratio/`,
    method: "GET"
  });
}

export function getReviewTimeConsumingTable(id: number, side: string) {
  return request({
    url: `/projects_review/${id}/time_consuming_table/`,
    method: "GET",
    params: { company_side: side }
  });
}
