import request from "@/apis/http";
import {
  DiscussionInput,
  Project,
  Projects,
  ProjectCreateInputs,
  StageCreateInputs,
  ProjectsParams,
  ProjectUpdateJoinedMemberParams,
  StageMediaParams,
  StageUpdate
} from "@/typings/project";
import { AxiosPromise } from "axios";
import { OptionParams } from "@/typings/collection";

export function getAvaliableOptions(
  trialType: string,
  collectionId?: number | null,
  fabricId?: number | null
) {
  const params: { collection_id?: number; fabric_id?: number } = {};
  if (collectionId) {
    params.collection_id = collectionId;
  }
  if (fabricId) {
    params.fabric_id = fabricId;
  }
  return request({
    url: `/get_avaliable_brands/${trialType}`,
    method: "GET",
    params
  });
}

export function getProjects(params: ProjectsParams): AxiosPromise<Projects> {
  return request({
    url: `/projects/`,
    method: "GET",
    params
  });
}

export function getProject(id: number): AxiosPromise<Project> {
  return request({
    url: `/project/${id}/`,
    method: "GET"
  });
}

export function postCreateProject(payload: ProjectCreateInputs) {
  return request({
    url: `/projects/`,
    method: "POST",
    data: payload
  });
}

export function postCreateStage(payload: StageCreateInputs) {
  const formData = new FormData();
  formData.append("name", payload.name);
  formData.append("description", payload.description);
  formData.append("project", payload.project.toString());
  formData.append("side", payload.side.toString());
  formData.append("price", payload.price.toString());
  if (payload.files) {
    for (let i = 0; i < payload.files.length; i++) {
      formData.append("files", payload.files[i]);
    }
  }
  return request({
    url: `/stages/`,
    method: "POST",
    data: formData
  });
}

export function postUpdateStage(payload: StageUpdate) {
  return request({
    url: `/stage/${payload.id}`,
    method: "PUT",
    data: {
      name: payload.name,
      price: payload.price,
      description: payload.description
    }
  });
}

export function getProjectStage(id: number) {
  return request({
    url: `/stage/${id}`,
    method: "GET"
  });
}

export function postCreateDiscussion(payload: DiscussionInput) {
  const formData = new FormData();
  if (payload.files) {
    for (let i = 0; i < payload.files.length; i++) {
      formData.append("files", payload.files[i]);
    }
  }
  if (payload.message) formData.append("message", payload.message);
  if (payload.project) formData.append("project", payload.project.toString());
  return request({
    url: `/discussions/`,
    method: "POST",
    data: formData
  });
}

export function postDeleteDiscussion(id: number) {
  return request({
    url: `/discussion/${id}`,
    method: "DELETE"
  });
}

export function postEditDiscussion(id: number, message: string) {
  const data = new FormData();
  data.append("message", message);

  return request({
    url: `/discussion/${id}`,
    method: "PUT",
    data
  });
}

export function deleteDicussionFile(id: number) {
  return request({
    url: `/attachment/${id}`,
    method: "DELETE"
  });
}

export function getProjectAvailableMember(data: OptionParams) {
  const params: { q?: string } = {};
  if (data.q) params.q = data.q;

  return request({
    url: `/project/${data.id}/avaliable_memebr`,
    method: "GET",
    params
  });
}

export function getProjectJoinedMembers(id: number) {
  return request({
    url: `/project/${id}/joined_member`,
    method: "GET"
  });
}

export function postProjectJoinedMemberUpdate({
  id,
  joined_member
}: ProjectUpdateJoinedMemberParams) {
  return request({
    url: `/project/${id}/joined_member`,
    method: "PUT",
    data: { joined_member }
  });
}

export function postProjectStatusUpdate({
  id,
  success,
  reason
}: {
  id: number;
  success: boolean;
  reason: string;
}) {
  return request({
    url: `/project/${id}/status`,
    method: "PUT",
    data: { success, reason }
  });
}

export function postStageMediaCreate(data: StageMediaParams) {
  const formData = new FormData();
  formData.append("stage", `${data.stage}`);
  if (data.file) formData.append("file", data.file);
  if (data.demo_fabric) formData.append("demo_fabric", `${data.demo_fabric}`);
  return request({
    url: `/stagemedias/`,
    method: "POST",
    data: formData
  });
}

export function getStagenames(id: number, companyId: number) {
  return request({
    url: `/project/${id}/stagename/${companyId}`,
    method: "GET"
  });
}

export function getInform(id: number) {
  return request({
    url: `/project/${id}/inform/`,
    method: "GET",
  });
}

export function postInform(id: number, subject: string, text: string, emails: string[]) {
  const params = {
    subject,
    message: text,
    recipients: emails
  };
  return request({
    url: `/project/${id}/inform/`,
    method: "GET",
    params
  });
}
