<template>
  <div id="doughnut-chart-component">
    <div
      v-if="data.length"
      class="doughnut-chart-main"
      id="doughnut-chart-main"
    ></div>
    <div v-else class="empty">
      <p>No Data</p>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, watch, nextTick, PropType } from "vue";
import * as echarts from "echarts/core";

import { PieChart, PieSeriesOption } from "echarts/charts";
import {
  GridComponent,
  GridComponentOption,
  TooltipComponent,
  TooltipComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";
import { AddResizer } from "@/typings/store";
import { useStore } from "vuex";

type ECOption = echarts.ComposeOption<
  PieSeriesOption | GridComponentOption | TooltipComponentOption
>;

echarts.use([PieChart, GridComponent, CanvasRenderer, TooltipComponent]);

type DataItem = {
  name: string;
  count: number;
};

const Component = defineComponent({
  name: "DoughnutChart",
  props: {
    data: {
      type: Array as PropType<DataItem[]>,
      default: () => []
    }
  },
  setup(props, { emit }) {
    const store = useStore()
    const { data } = toRefs(props);

    const adapter: Adapter<string> = {
      "In Progress": "#5470c6",
      "Completed but delayed": "#91cc76",
      "Completed on time": "#409eff",
      Dropped: "#e66",
      Delayed: "#f8c858",
      default: "#5470c6"
    };

    watch([data], async () => {
      const option: ECOption = {
        tooltip: {
          trigger: "item",
          formatter: "{b}: {c}"
        },

        series: [
          {
            type: "pie",
            radius: ["60%", "90%"],
            label: {
              show: false,
              position: "center"
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "20",
                fontWeight: "bold"
              }
            },
            labelLine: {
              show: false
            },
            itemStyle: {
              borderRadius: 10,
              borderColor: "white",
              borderWidth: 2
            },
            data: data.value.map(it => ({
              name: it.name,
              value: it.count,
              itemStyle: {
                // @ts-ignore
                color: adapter[it.name] || adapter.default
              }
            }))
          }
        ]
      };

      await nextTick(() => {
        const chart = echarts.init(
          document.getElementById("doughnut-chart-main")!
        );
        chart.setOption(option);
        chart.on("click", params => {
          emit("chart-click", params);
        });
        store.commit(AddResizer, chart)
      });
    });
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#doughnut-chart-component
  width 100%
  background-color white
  .doughnut-chart-main
    width 100%
    padding 15px 0
    height 350px

  .empty
    flex()
    height 350px
    font-size $font-size-lg3x
    color $color-gray
</style>
