export function postMember(params: Member, id?: number) {
  const data = {
    email: params.email,
    nickname: params.nickname,
    role: params.role,
    teams: params.teams,
    phone: params.phone,
    active: params.active,
    business_unit: params.business_unit
  };
  if (id) {
    return request({
      url: `/member/${id}/`,
      method: "PUT",
      data
    });
  }
  return request({
    url: `/member/`,
    method: "PUT",
    data
  });
}

export function postMemberPassword(data: { password: string }) {
  return request({
    url: `/member/`,
    method: "PUT",
    data
  });
}

export function postMemberAvatar(file: File, id?: number) {
  const data = new FormData();
  data.append("file", file);
  if (id) data.append("member", JSON.stringify(id));

  return request({
    url: "/member/medias/",
    method: "POST",
    data
  });
}

export function postContactUs(data: ContactUsParams) {
  return request({
    url: `/mail/contact_us/`,
    method: "POST",
    data
  });
}
