<template>
  <div id="progress-chart-component">
    <div v-if="data.length" class="progress-wrapper">
      <div class="name-wrapper">
        <div v-for="(item, index) in data" :key="item.source" class="name-item">
          <div class="name-color" :style="getNameColorStyles(index)"></div>
          <h2>From {{ item.source }}</h2>
        </div>
      </div>
      <div class="progress-bar-wrapper">
        <div class="bar-wrapper">
          <div
            v-for="(item, index) in data"
            :key="index"
            :style="getBarStyles(item.count, index)"
            class="bar"
          >
            {{ item.count }}
          </div>
        </div>
      </div>
    </div>
    <p class="empty" v-else>No Data</p>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType, computed } from "vue";

const colors = ["#5470c6", "#91cc76", "#f8c858", "#e66"];
const getNameColorStyles = (index: number) => ({
  backgroundColor: colors[index]
});

const Component = defineComponent({
  name: "ProgressChart",
  props: {
    data: {
      type: Array as PropType<{ source: string; count: number }[]>,
      default: () => []
    }
  },
  setup(props) {
    const { data } = toRefs(props);
    const total = computed(() =>
      data.value.reduce((prev, curr) => prev + curr.count, 0)
    );

    const getBarStyles = (count: number, index: number) => {
      return {
        background: colors[index],
        width: `calc(100% / ${total.value} * ${count})`
      };
    };

    return {
      getBarStyles,
      getNameColorStyles
    };
  }
});

export default Component;
</script>

