<template>
  <div id="review-component">
    <div class="search-container">
      <div class="inline-item">
        <div class="label">Team</div>
        <el-select v-model="form.team">
          <el-option
            v-for="item in teamOptions"
            :key="item.value"
            :value="item.value"
            :label="item.label"
          ></el-option>
        </el-select>
      </div>

      <div class="search-options-wrapper">
        <div class="card">
          <div class="hint-wrapper">
            <img :src="HintIcon" class="icon-hint" alt="Hint icon" />
            <p>For all projects</p>
          </div>
          <div class="item">
            <div class="label">Year</div>
            <el-select v-model="form.year">
              <el-option
                v-for="item in yearOptions"
                :key="item.value"
                :value="item.value"
                :label="item.label"
              ></el-option>
            </el-select>
          </div>
          <div class="item">
            <div class="label">Season</div>
            <el-select v-model="form.season" multiple placeholder="Select...">
              <el-option
                v-for="item in seasonOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="line"></div>
        <div class="card">
          <div class="hint-wrapper">
            <img :src="HintIcon" class="icon-hint" alt="Hint icon" />
            <p>
              Expected End Date for uncompleted projects; Actual End Date for
              completed projects
            </p>
          </div>
          <div class="item">
            <div class="label">Initiate Date</div>
            <el-date-picker
              v-model="form.initiateDate"
              type="daterange"
              value-format="YYYY-MM-DD"
              range-separator="to"
              start-placeholder="Start Date"
              end-placeholder="End Date"
            >
            </el-date-picker>
          </div>
          <div class="item">
            <div class="label">Expected End Date</div>
            <el-date-picker
              v-model="form.expectedEndDate"
              type="daterange"
              value-format="YYYY-MM-DD"
              range-separator="to"
              start-placeholder="Start Date"
              end-placeholder="End Date"
            >
            </el-date-picker>
          </div>
          <div class="item">
            <div class="label">Actual End Date</div>
            <el-date-picker
              v-model="form.actualEndDate"
              type="daterange"
              value-format="YYYY-MM-DD"
              range-separator="to"
              start-placeholder="Start Date"
              end-placeholder="End Date"
            >
            </el-date-picker>
          </div>
        </div>
      </div>
      <div class="button-wrapper">
        <el-button class="button" type="success" @click="onSearch">
          Search
        </el-button>
      </div>
    </div>
    <el-row :gutter="60">
      <el-col :span="12">
        <div class="status-card">
          <h2>Status</h2>
          <DoughnutChart
            :data="pieData"
            @chart-click="onDoughnutClick"
          ></DoughnutChart>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="source-card">
          <h2>Source</h2>
          <ProgressChart :data="progressData"></ProgressChart>
        </div>
        <div class="success-rate-card">
          <h2>Success Rate</h2>
          <SuccessRateChart :data="rateData"></SuccessRateChart>
        </div>
      </el-col>
    </el-row>
    <div class="zoom-title-container">
      <div class="project-title-wrapper">
        <h2 class="zoom-title">{{ statusValue || "All Projects" }}</h2>
        <el-button v-if="statusValue" type="success" @click="onStatusClear">
          Clear
        </el-button>
      </div>
      <div class="search-bar-wrapper">
        <p>Search:</p>
        <el-input
          v-model="searchProjectValue"
          placeholder="Projects..."
        ></el-input>
      </div>
    </div>
    <ReviewTable :data="tableData"> </ReviewTable>
    <div class="pagination-wrapper">
      <el-pagination
        v-if="pageCount"
        background
        layout="prev, pager, next"
        :current-page="page"
        :page-count="pageCount"
        @current-change="onPaginationChange"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive, watch, onMounted } from "vue";
import DoughnutChart from "@/components/DoughnutChart.vue";
import ProgressChart from "./ProgressChart.vue";
import SuccessRateChart from "./SuccessRateChart.vue";
import ReviewTable from "./ReviewTable.vue";
import { ReviewsParams } from "@/typings/review";
import {
  getReviewSourceRatio,
  getReviewStatusPie,
  getReviewSuccessRate
} from "@/apis/review";
import { getProjects } from "@/apis/project";
import { Projects, ProjectsParams } from "@/typings/project";
import { OptionItem } from "@/typings/asideFilter";
import { getMemberTeams } from "@/apis/member";
import { MemberTeam } from "@/typings/member";
import HintIcon from "@/assets/images/icons/png/hint.png";

import { yearOptions, seasonOptions } from "@/views/Collection/options";

const Component = defineComponent({
  name: "Review",
  components: {
    DoughnutChart,
    ProgressChart,
    SuccessRateChart,
    ReviewTable
  },
  setup() {
    const teamOptions = ref<OptionItem[]>([]);
    const form = reactive<{
      team: number;
      year: number | null;
      season: string[];
      initiateDate: string[] | null;
      expectedEndDate: string[] | null;
      actualEndDate: string[] | null;
    }>({
      team: -1,
      year: 2021,
      season: ["SPRING", "SUMMER"],
      initiateDate: null,
      expectedEndDate: null,
      actualEndDate: null
    });
    const isSearch = ref(false);
    const statusValue = ref("");
    const searchProjectValue = ref("");
    const pageCount = ref(0);
    const page = ref(1);
    const tableData = ref<Projects | null>(null);
    const pieData = ref([]);
    const progressData = ref([]);
    const rateData = ref<{} | null>(null);

    const fetchProjects = async () => {
      const tableParams: ProjectsParams = {
        page: page.value
      };

      if (form.team > 0) {
        tableParams.team = form.team;
      }
      if (form.year) {
        tableParams.year = form.year;
      }
      if (form.season) {
        tableParams.season = form.season;
      }

      if (form.initiateDate) {
        tableParams.initiate_date = form.initiateDate;
      }
      if (form.expectedEndDate) {
        tableParams.expected_end_date = form.expectedEndDate;
      }
      if (form.actualEndDate) {
        tableParams.actual_end_date = form.actualEndDate;
      }

      if (searchProjectValue.value) {
        tableParams.q = searchProjectValue.value;
      }
      if (statusValue.value) {
        tableParams.status = statusValue.value;
      }

      const { data: table } = await getProjects(tableParams);
      tableData.value = table;
      pageCount.value = table.total_pages;
    };

    const onSearch = async () => {
      const params: ReviewsParams = {};

      if (form.team > 0) {
        params.team = form.team;
      }
      if (form.year) {
        params.year = form.year;
      }
      if (form.season) {
        params.season = form.season;
      }
      if (form.initiateDate) {
        params.initiate_date = form.initiateDate;
      }
      if (form.expectedEndDate) {
        params.expected_end_date = form.expectedEndDate;
      }
      if (form.actualEndDate) {
        params.actual_end_date = form.actualEndDate;
      }

      // Reset
      page.value = 1;
      statusValue.value = "";

      const { data: progress } = await getReviewSourceRatio(params);
      const { data: pie } = await getReviewStatusPie(params);
      const { data: rate } = await getReviewSuccessRate(params);
      await fetchProjects();

      isSearch.value = true;
      pieData.value = pie;
      progressData.value = progress;
      rateData.value = rate;
    };

    const init = async () => {
      const { data: memberTeams } = await getMemberTeams(0);
      const options = memberTeams.map((it: MemberTeam) => ({
        label: it.name,
        value: it.id,
        checked: false
      }));
      if (options.length > 1) {
        options.unshift({
          label: "All",
          value: -1,
          checked: false
        });
      }
      teamOptions.value = options;
      form.team = options[0].value;
      await onSearch();
    };

    onMounted(init);

    const clearForm = (type: string) => {
      if (type === "time") {
        if (form.initiateDate) form.initiateDate = null;
        if (form.expectedEndDate) form.expectedEndDate = null;
        if (form.actualEndDate) form.actualEndDate = null;
      }
      if (type === "date") {
        if (form.year) form.year = null;
        if (form.season.length) form.season = [];
      }
    };

    watch([searchProjectValue, page], async () => {
      if (!isSearch.value) return;
      await fetchProjects();
    });

    watch(
      () => form.year,
      () => form.year && clearForm("time")
    );

    watch(
      () => form.season,
      () => form.season.length && clearForm("time")
    );

    watch(
      () => form.initiateDate,
      () => form.initiateDate && clearForm("date")
    );
    watch(
      () => form.expectedEndDate,
      () => form.expectedEndDate && clearForm("date")
    );
    watch(
      () => form.actualEndDate,
      () => form.actualEndDate && clearForm("date")
    );

    const onDoughnutClick = async (data: { type: string; name: string }) => {
      if (data.type === "click") {
        statusValue.value = data.name;
        await fetchProjects();
      }
    };

    const onStatusClear = async () => {
      statusValue.value = "";
      await fetchProjects();
    };

    const onPaginationChange = (_page: number) => {
      page.value = _page;
    };

    return {
      form,
      searchProjectValue,
      pageCount,
      page,
      statusValue,
      teamOptions,
      pieData,
      progressData,
      rateData,
      tableData,
      HintIcon,
      yearOptions,
      seasonOptions,

      onSearch,
      onDoughnutClick,
      onStatusClear,
      onPaginationChange
    };
  }
});

export default Component;
</script>

